.hamburger {
  position: fixed;
  right: 10px;
  top: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  border: 3px black solid;
  padding: 8px 4px 4px 4px;
  transition: 0.4s;
}

.hamburger:hover {
  background-color: rgba(255, 255, 255, 1);
  /* https://jonsuh.com/hamburgers/ */
}
