@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-image: url(/react-weather-api/static/media/summer.124293f9.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.app_wrapper {
  margin: 0 auto;
  min-height: 100vh;
  min-width: 650px;
  max-width: 1000px;
  padding: 5vh 0;
  display: flex;
  justify-content: space-around;
}
.app_left_container,
.app_right_container {
  flex-basis: 45%;
  background-color: rgba(255, 255, 255, 0.5);
  min-width: 300px;
  display: flex;
  flex-direction: column;
}

.app_logo {
  font-family: "Nerko One", cursive;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
}

.app_logo_container {
  display: inline-block;
  position: relative;
}

.app_logo_header {
  position: relative;
  font-size: 3rem;
  z-index: 3;
  color: cadetblue;
  text-shadow: 0 0 10px white;
}

.app_logo_description {
  font-size: 1rem;
  position: relative;
  z-index: 3;
}

.app_logo_img {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  z-index: 2;
  animation: rotateSun 25s infinite linear;
}

@keyframes rotateSun {
  from {
    transform: translateX(50%) rotate(0deg);
  }
  to {
    transform: translateX(50%) rotate(360deg);
  }
}

.app_input_container,
.app_input_results {
  margin: 10px 10px;
}

.app_input_label {
  color: grey;
  font-weight: 500;
  font-style: italic;
  font-size: 0.8rem;
}

.app_input_town {
  width: 100%;
  padding: 5px;
  font-weight: bold;
  font-size: 1rem;
  margin-top: 3px;
  border: 2px solid black;
}

.app_input_results {
  background-color: rgba(255, 255, 255, 0.5);
  flex-grow: 1;
  border: 2px solid black;
}

aside {
  position: fixed;
  top: 0;
  left: -300px;
  height: 100vh;
  width: 300px;
  background-color: cadetblue;
  border-right: 5px white solid;
  transition: 0.4s;
  z-index: 999;
  padding: 0 10px;
}

aside.sideMenuActive {
  left: 0;
}

.searchResults_wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 5px 10px;
}

.searchResults_item {
  border: 2px solid black;
  padding: 3px 2px 3px 5px;
  cursor: pointer;
  transition: 0.3s;
  margin: 2px 0;
}

.searchResults_item:nth-of-type(2n) {
  background-color: white;
}
.searchResults_item:nth-of-type(2n + 1) {
  background-color: cadetblue;
}

.searchResults_item:hover {
  background-color: yellow;
}

.searchResults_header {
  font-size: 1.2rem;
  color: darkslategrey;
}

.searchResults_area {
  color: darkslategray;
  font-style: italic;
  font-size: 0.8rem;
}

.searchResults_more {
  border: 2px solid black;
  background-color: green;
  text-align: center;
  padding: 10px 0;
  font-weight: bold;
  color: white;
  font-size: 1.4rem;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.searchResults_alert {
  border: 2px solid black;
  background-color: darkcyan;
  margin: 0 10px;
  text-align: center;
  padding: 10px 0;
  font-weight: bold;
  color: white;
  font-size: 1.4rem;
}

.weather_wrapper {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}

.weather_name,
.weather_temp,
.weather_description {
  font-size: 1.4rem;
  text-align: center;
}

.weather_temp {
  height: 200px;
  width: 200px;
  background-color: cadetblue;
  border-radius: 50%;
  display: flex;
  align-items: center;
  box-shadow: 0 0 5px white;
  border: 3px solid black;
}

.weather_wrapper span {
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  text-shadow: 1px 1px 2px black;
}

.weather_info {
  margin-top: 30px;
  text-align: center;
  height: 100%;
  font-size: 1.5rem;
  font-weight: bold;
  text-transform: uppercase;
  color: cadetblue;
  text-shadow: 1px 1px 2px white;
}

.saveBtn {
  background-color: cadetblue;
  font-size: 1.2rem;
  padding: 10px;
  cursor: pointer;
  color: white;
  border: 3px white solid;
  transition: 0.4s;
}

.saveBtn:hover {
  color: black;
}

.hamburger {
  position: fixed;
  right: 10px;
  top: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  border: 3px black solid;
  padding: 8px 4px 4px 4px;
  transition: 0.4s;
}

.hamburger:hover {
  background-color: rgba(255, 255, 255, 1);
  /* https://jonsuh.com/hamburgers/ */
}

.sideMenu_header {
  text-transform: uppercase;
  color: black;
  text-shadow: 1px 1px 2px white;
  text-align: center;
  margin: 10px 0;
}

.sideMenu_clearBtn {
  font-size: 1.2rem;
  padding: 10px;
  font-weight: bold;
  border: 3px solid black;
  display: block;
  margin: 0 auto;
  transition: 0.3s;
}

.sideMenu_clearBtn:hover {
  color: white;
  background-color: black;
  border-color: white;
}

.sideMenu_town {
  margin: 10px;
  color: white;
  text-shadow: 1px 1px 2px black;
  font-size: 1.2rem;
  cursor: pointer;
}

.sideMenu_town::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: black;
  box-shadow: 1px 1px 2px white;
}

.sideMenu_town:hover {
  color: yellow;
}

.sideMenu_townsInfo {
  text-align: center;
  color: white;
}

